.container{
  width: 900px;
  border: 1px solid black;
  padding: 50px;
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.959);
  box-shadow: rgb(38, 77, 48) 0px 20px 30px -10px;
}
.form{
}
.price{
  display: flex;
  align-items: center;
}
.priceText{
  font-size: 20px
}
.priceText span{
  color:chocolate;
  font-size: 22px;
}
.form >*:not(:last-child){
  margin-bottom: 10px;

}
.input{
  border: 1px solid black;
  height: 30px;
  padding: 0 10px;
  height: 45px;
}

.inp{
  height: 100%;
  width: 100%;
  font-size: 25px;
}
.button{
  padding: 15px 0px;
  width: 100%;
  margin: 10px 0px;
  border-radius: 10px;
  box-shadow: 0 0 40px 40px gray inset, 0 0 0 0 #1d9f82;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  letter-spacing: 2px;
  color: white;
  transition: .15s ease-in-out;
}
.button:hover{
  box-shadow: 0 0 10px 0 #145547 inset, 0 0 10px 4px #23231a;
  color: #040404;
}

@media(max-width:1000px){
  .container{
    width: 600px;
    padding: 20px 40px 40px 40px;
  }
}
@media(max-width:680px){
  .container{
    width: 400px;
  }
}
@media(max-width:480px){
  .container{
    padding: 10px 20px 20px 20px;
    margin: 0 auto 200px auto;
    /* flex:1 1 100%; */
    /* margin: 0 auto; */
    width:90%;
  }


}